<template>
    <div>
      <el-col :span="24">
        <el-form :inline="true" :model="formInline" class="demo-form-inline" size="small">
          <el-form-item label="缴费类型">
              <el-select v-model="formInline.value1" placeholder="请选择缴费类型" clearable>
                  <el-option label="体检缴费" value="体检缴费"></el-option>
                  <el-option label="照相缴费" value="照相缴费"></el-option>
                </el-select>
          </el-form-item>
          <el-form-item label="支付方式" v-if="modeClick">
              <el-select v-model="formInline.value2" placeholder="请选择支付方式" clearable>
                  <el-option label="微信" value="微信"></el-option>
                  <el-option label="支付宝" value="支付宝"></el-option>
                  <el-option label="现金" value="现金"></el-option>
                </el-select>
          </el-form-item>
          <el-form-item label="日期选择">
            <el-date-picker
                  v-model="formInline.value3"
                  type="daterange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期">
                </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="downList">下载统计表</el-button>
            <span style="margin: 10px 0 10px 20px;color: red;">注：默认统计本日数据，包含所有收费成功数据(设备收费净收入=总收入-退款数)。</span>
          </el-form-item>
        </el-form>
      </el-col>
      <el-card>
        <el-row><el-col :span="4" :push="20"><el-button @click="modeSwitch">{{modeDeom}}</el-button></el-col></el-row>
        <ve-histogram :data="chartData" v-if="modeClick"></ve-histogram>
         <el-table
            v-else
            :data="chartData.rows"
            style="width: 100%">
            <el-table-column
              prop="设备"
              label="设备编号"
              width="180">
            </el-table-column>
            <el-table-column
              prop="支付宝收款"
              label="支付宝收款"
              width="125">
            </el-table-column>
            <el-table-column
              prop="微信收款"
              label="微信收款"
              width="125">
            </el-table-column>
            <el-table-column
              prop="现金收款"
              label="现金收款"
              width="125">
            </el-table-column>
            <el-table-column
              prop="退款金额"
              label="退款金额"
              width="125">
            </el-table-column>
            <el-table-column
              prop="净收入"
              label="净收入"
              width="125">
            </el-table-column>
            <el-table-column
              prop="总金额"
              label="总数"
              width="125">
            </el-table-column>
          </el-table>
      </el-card>
    </div>
</template>
<script>
    import VeHistogram from 'v-charts/lib/histogram'
  	export default {
      data(){
        return{
          chartData:'',
          formInline:{
            value1:'',
            value2:'',
            value3:''
          },
          modeDeom:'表格方式',
          modeClick:true
        }
      },
      methods:{
        onSubmit(){
          console.log('查询')
        },
        downList(){
          console.log('下载统计表')
        },
        modeSwitch(){
          if(this.modeClick){
            this.modeClick = false
            this.modeDeom = '图表方式'
          }else{
            this.modeClick = true
            this.modeDeom = '表格方式'
          }
        }
      },
      created () {
          this.chartData = {
            columns: ['设备','总金额','人次'],
            rows: [
              { '设备': 'tjj001', '总金额': 1232, '人次':123,'支付宝收款':342,'微信收款':213,'现金收款':435,'退款金额':132,'净收入':1120},
              { '设备': 'tjj002', '总金额': 1223 , '人次':232,'支付宝收款':343,'微信收款':243,'现金收款':424,'退款金额':134,'净收入':1121},
              { '设备': 'tjj003', '总金额': 2123 , '人次':443,'支付宝收款':344,'微信收款':256,'现金收款':478,'退款金额':155,'净收入':1122},
              { '设备': 'tjj004', '总金额': 4123 , '人次':231,'支付宝收款':345,'微信收款':276,'现金收款':499,'退款金额':166,'净收入':1123},
              { '设备': 'tjj005', '总金额': 3123 , '人次':223,'支付宝收款':346,'微信收款':234,'现金收款':497,'退款金额':178,'净收入':1124},
              { '设备': 'tjj006', '总金额': 1000 , '人次':121,'支付宝收款':347,'微信收款':266,'现金收款':467,'退款金额':144,'净收入':1125}
            ]
          }
        },
        components: { VeHistogram }
    }
</script>
